* {
  margin: 0px;
  padding: 0px; }

body {
  width: 100%;
  height: 100vh;
  font-family: 'Lato', sans-serif;
  color: #A4A5A6; }

.hero {
  height: 100%;
  display: flex; }

.hero__avatar-container {
  background-color: #2C2C2C;
  width: 30%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center; }
  .hero__avatar-container .hero__avatar {
    border-radius: 50%;
    width: 210px; }

.hero__info-container {
  position: relative;
  background-color: #2E2E2E;
  width: 70%;
  height: 100%; }

.hero__info-container-section {
  max-width: 735px;
  margin: 0 0 0 60px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center; }

.hero__header {
  width: 100%; }
  .hero__header p {
    display: inline-block;
    margin-top: 15px;
    font-size: 18px;
    font-weight: 100;
    font-style: italic; }

.hero__titleA {
  display: block;
  text-transform: lowercase;
  font-size: 2.8rem;
  font-weight: 300; }

.hero__titleB {
  display: block;
  text-transform: uppercase;
  color: #fff;
  font-size: 3rem; }

.hero__subtitle {
  width: 100%;
  display: flex;
  justify-content: space-between; }

.hero__list-social {
  list-style: none;
  display: flex; }
  .hero__list-social li {
    margin: 0 5px 0 0; }
    .hero__list-social li a {
      font-size: 2rem; }
      .hero__list-social li a i {
        color: #A4A5A6;
        transition: color 1s; }
        .hero__list-social li a i:hover {
          color: #fff; }

.hero__contact {
  display: block;
  position: absolute;
  bottom: 0;
  border-top: 1px solid #A4A5A6;
  width: 89%;
  max-width: 735px; }

.hero__contact-container {
  display: flex;
  justify-content: space-between; }

.hero__contact-item {
  margin-top: 30px;
  margin-bottom: 50px; }
  .hero__contact-item h3 {
    margin-bottom: 15px;
    font-size: 0.9rem;
    font-weight: bold;
    color: #fff; }
  .hero__contact-item p {
    font-size: 0.9rem;
    font-weight: normal; }
  .hero__contact-item a {
    text-decoration: none;
    color: #A4A5A6;
    transition: color 0.5s; }
    .hero__contact-item a:hover {
      color: #fff; }
